import opg from 'ts/opg';

import {Languages} from 'ts/util/Languages';
import {Cache} from 'ts/util/store';
import {url} from 'ts/util/utils';
import PopUp from 'ts/ui/Popup';
import {Combo} from "../../../ts/ui/Combo";

const cache = Cache.getInstance();

const row = cache.get('row');
console.log(row);

const lpg = Languages.package;
console.log('Languages:', Languages);


opg.api({
	'addMain!post': 'activity/AddInfomationSubject',
	'updateMain!post': 'activity/UpActivitySubject',
	getSubjects: 'activity/GetActivityContentsByActivityId',
	delSubject: 'activity/DelActivityContentById',
	'upload!UPLOAD': 'upload/files',
});


let ID;
const subjectPage = __uri('/pages/content/activity/subject.html');


jQuery.datetimepicker.setLocale(Languages.current);
$('#beginDate,#endDate').each(function (i, elem) {
	$(elem).datetimepicker({
		//timepicker: false,
		//closeOnDateSelect: true,
		format: 'Y-m-d H:i:00',
		step: 30,
	});
});


let iptCoverPicture = $('#coverPicture');
let tdCover = $('#tdCover');
Combo.makeClearableInput(iptCoverPicture, $({}));
$('.ipt-eraser' , '#tdUpload').on('click' , function () {
	//if(!this.value){
	tdCover.empty();
	//}
});

let uploadForm = $(`<form>
						<input name="file" type="file" accept="image/jpg,image/jpeg,image/png">
				</form>`);
$('#btnUploadDialog').click(function () {
	let pop: PopUp = top.opg.confirm(uploadForm, function () {
		let formData = new FormData(uploadForm[0] as HTMLFormElement);
		opg.api.upload(formData, function (data) {
			iptCoverPicture.val(data[0].downloadLink);
			tdCover.html(`<img src="${data[0].downloadLink}"/>`);
			pop.close();
		});
		return true;
	}, {
		title: `${lpg.upload}`,
		width: 200,
		height: 50,
		buttons: {
			ok: `${lpg.upload}`,
			cancel: `${lpg.cancel}`,
		},
	});


});

const btnAddSubject = $('#btnAddSubject');
const $tbMain = $('#tbMain');

let tbSubjects = opg('#tbSubjects').table({
	api: opg.api.getSubjects,
	lazy: true,
	titleBar: {
		title: lpg.subjectsList,
	},
	columns: [
		{
			text: lpg.topic,
			src: 'title',
		},
		{
			text: lpg.time, width: 120,
			src: 'time',
		},
		{
			text: lpg.process,
			src: 'id',
			width: 120,
			render: (id, i, row) => {
				return `
					<button data-id="${id}" data-index="${row[':index']}" class="btn-mini btn-info btnEdit">${lpg.edit}</button> 
					<button data-id="${id}" class="btn-mini btn-danger btnDel">${lpg.del}</button> 
					`;

			},
		},
	],
});


//edit
tbSubjects.tbody.on('click', '.btnEdit', function () {
	let btn = $(this), id = btn.data('id'), index = btn.data('index');

	let row = tbSubjects.data[index];
	row.enableStick = row.stick ? 1 : 0;

	cache.set('rowSub', row);

	let src = url.setParam(subjectPage, {activityId: ID, subjectId: row.id});

	let pop = opg.popTop(`<iframe src="${src}" />`, {
		title: `${lpg.edit}: ${row.title}`,
		btnMax: true,
		width: 1100,
		height: 600,
		onClose: function () {
			cache.remove('rowSub');
			tbSubjects.update();
		},
	});
});


//delete
tbSubjects.tbody.on('click', '.btnDel', function () {
	let btn = $(this), id = btn.data('id') ;
	opg.confirm(lpg.subjectsDeleteConfirm ,  ()=> {
		opg.api.delSubject({id}, () => {
			tbSubjects.update();
		});
	});
});


if (row) {
	ID = row.id;
	btnAddSubject.show();
	$tbMain.jsonToFields(row);
	tbSubjects.update({id: ID});
}


$('#btnSaveMain').click(function () {
	let param = $tbMain.fieldsToJson({
		title: {
			name: Languages.package.title,
			type: 'ns',
			require: true,
		},
		publishbegindate: {
			name: Languages.package.publishTimeBegin,
			require: true,
		},
		publishenddate: {
			name: Languages.package.publishTimeEnd,
			require: true,
		},
	});

	if (param) {
		param.enableStick = param.enableStick ? 1 : 0;

		if (ID) {
			param.id = ID;
			param.stick = param.enableStick;
			opg.api.updateMain(param);
		}
		else {
			opg.api.addMain(param, (data) => {
				ID = data;
				btnAddSubject.show();
			});
		}


	}
});


btnAddSubject.click(function () {
	let src = url.setParam(subjectPage, {activityId: ID});
	let pop: PopUp = opg.popTop(`<iframe src="${src}" />`, {
		title: `${lpg.addSubject}`,
		width: 1100,
		height: 600,
		btnMax: true,
		onClose: function () {
			tbSubjects.update();
		},
	});
});
